/** navigation bar **/
nav {
  padding: 10px 10px 5px;
  box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, 0.5); }

nav .logo {
  color: #2b2b2b;
  font-size: 40px;
  font-family: "Avenir Bold"; }

nav .logo:hover {
  text-decoration: underline; }

nav .container span {
  font-size: 14px;
  font-family: "latothin"; }

/** header (home) **/
header.header-home {
  border-bottom: 10px solid #2b2b2b;
  padding: 30px; }

header.header-home h1 {
  margin: 30px;
  font-size: 140px; }

header.header-home h1 span {
  display: block;
  margin: -20px auto 0;
  max-width: 300px;
  font-family: "latothin";
  font-size: 24px;
  font-style: italic; }

/** articles **/
article.article-home {
  margin-bottom: 40px; }

article.article-home h2 {
  font-size: 48px; }

article.article-home span {
  display: block;
  margin-top: -5px;
  margin-bottom: 30px;
  font-size: 12px; }

article.article-home a {
  text-decoration: none; }

article.article-home .read-more-btn {
  position: relative;
  overflow: hidden;
  display: inline-block;
  margin: 30px auto;
  padding: 10px;
  background-color: #2b2b2b;
  color: #fff; }

article.article-home .read-more-btn:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  bottom: 2px;
  background-color: #fff;
  transform: translateX(-100%);
  transition: transform .34s ease; }

article.article-home .read-more-btn:hover {
  text-decoration: none; }
  article.article-home .read-more-btn:hover:before {
    transform: translateX(0%);
    transition: transform .34s ease; }

/** mainly post page stuff **/
header.header-post {
  width: 100%;
  height: 400px;
  position: relative;
  overflow: hidden; }

header.header-post h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 80%;
  max-width: 900px;
  z-index: 10;
  font-size: 64px;
  border-bottom: 10px solid;
  -moz-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%); }

.full-background {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center; }

header.header-post .full-background:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.6); }

.pagination, .pager {
  padding: 20px;
  text-align: center; }
  .pagination li, .pager li {
    min-width: 20px;
    margin: 0 5px;
    font-size: 50px;
    display: inline-block; }
  .pagination li.disabled, .pager li.disabled {
    cursor: default;
    opacity: .2; }
  .pagination li a, .pager li a {
    display: block;
    transform: translateX(0);
    transition: transform .34s ease; }
  .pagination a:hover, .pager a:hover {
    text-decoration: none; }
  .pagination li:hover + a, .pager li:hover + a {
    transform: translateX(-30%);
    transition: transform .34s ease; }
  .pagination li:hover a, .pager li:hover a {
    transform: translateX(30%);
    transition: transform .34s ease; }
